.contact {
  background: #05040b;
  position: relative;
  top: -5px;
  &__contant {
    padding: 0 10%;
    height: 589px;
    background: #05040b;
    display: flex;
    align-items: center;
    overflow: hidden;
    @media screen and (max-width: 1050px) {
      flex-direction: column;
      padding: 0 10%;
      height: 100%;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    width: 520px;
    @media screen and (max-width: 1050px) {
      width: 100%;
    }
  }

  &__input {
    outline: none;
    background-color: #05040b;
    border: none;
    border-bottom: 1px solid #2f2f2f;
    color: #929292;
    font-family: "Balsamiq Sans", sans-serif;
    line-height: 64px;
    font-size: 14px;
    &:last-child {
      height: 150px;
    }
  }
}

.info {
  position: relative;
  top: -3%;
  margin-left: 115px;
  display: flex;
  flex-direction: column;
  @media screen and (max-width: 1050px) {
    margin: 0;
    text-align: center;
  }

  &__title {
    @extend %title;
    margin-top: 0;
    margin-bottom: 0;
    &--accent {
      @extend %title-accent;
    }
  }

  &__top {
    position: relative;
    left: -30px;
    font-size: 14px;
    line-height: 18px;
    color: #05c2df;
    font-family: "Balsamiq Sans", sans-serif;
  }

  &__text {
    text-decoration: none;
    font-family: "Balsamiq Sans", sans-serif;
    font-size: 16px;
    line-height: 150%;
    color: #929292;
    margin-bottom: 5px;
    &:nth-child(3) {
      margin-bottom: 52px;
    }
  }

  &__button {
    background: #05c2df;
    border-radius: 4px;
    width: 200px;
    height: 50px;
    border: none;
    font-family: "Balsamiq Sans", sans-serif;
    color: #fff;
    margin-top: 50px;
    @media screen and (max-width: 1050px) {
      margin: 5% 50%;
      transform: translate(-50%);
    }
  }
}
